.hotel {
  border-radius: 15px;
  background-color: #232323;
  border: 1px #ffffff solid;
}

.preview {
  position: relative;
  margin-bottom: 10px;

  img {
    width: 100%;
  }
}

.title {
  padding: 5px 15px;
  position: absolute;
  width: 100%;
  bottom: 0;
  background: linear-gradient(180deg, rgba(44, 49, 127, 0.58) 0%, #2C317F 100%);
  border-radius: 15px;
  margin-top: -10px;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
}

.address {
  display: flex;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
  margin-bottom: 10px;
  font-family: Poppins, sans-serif;

  img {
    margin-right: 5px;
  }
}

.info {
  padding: 0 15px;
}

.advantages {
  font-weight: 400;
  font-size: 14px;
  line-height: 15px;
  color: #FFFFFF;
}

.rating {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  color: #FFFFFF;
  font-family: Poppins, sans-serif;
}

.stars {
  display: flex;
  margin-left: 10px;

  img {
    margin-right: 5px;
    height: 19px;
  }
}

.disabled_star {
  opacity: 0.2;
}

.price {
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;

  b {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
  }
}

.rating_price_block {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  margin-top: 15px;

}