@import '_fonts';
@import '_normalize';

@mixin transition02 {
  transition: 0.2s;
}

.leaflet-control-attribution{
  display: none !important;
}

#root {
  position: relative;
}

body {
  height: 100%;
  font-family: 'Raleway', sans-serif;
  .wrapper {
    position: relative;
    min-height: 100%;
    display: flex;
    flex-direction: column;
  }
}
.wrapper-signin {
  .breadcrumb {
    background-image: none;
  }
  .header {
    background-image: none;
  }
}
.container {
  max-width: 1140px;
  @media (max-width: 991px) {
    max-width: 732px !important;
  }
}
.indent-main {
  padding-top: 137px;
  padding-bottom: 468px;
  @media screen and (max-width: 1199px) {
    padding-top: 96px;
  }
  @media screen and (max-width: 991px) {
    padding-top: 63px;
    padding-bottom: 270px;
  }
  @media screen and (max-width: 767px) {
    padding-bottom: 228px;
    padding-top: 63px;
  }
}
.btn {
  padding: 0;
  border: 0;
  background-color: transparent;
}
.header {
  z-index: 99;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-image: url(../img/bg-header.png);
  background-size: cover;
  background-position: center center;

  &__blackout {
    background-color: rgba(0, 0, 0, 0.7);
  }
  &__nav {
    padding-top: 35px;
    padding-bottom: 40px;
    height: 137px;
    @media screen and (max-width: 1199px) {
      padding-top: 20px;
      padding-bottom: 20px;
      height: 96px;
    }
    @media screen and (max-width: 991px) {
      padding-top: 14px;
      padding-bottom: 14px;
      height: 63px;
    }
    @media screen and (max-width: 767px) {
      padding-top: 18px;
      padding-bottom: 21px;
    }
  }

  &__btn {
    &:hover {
      color: #fec87e;
      @include transition02;
    }
    img {
      width: calc(27px + (36 - 27) * ((100vw - 768px) / (1920 - 768)));
      margin-right: calc(13px + (7 - 13) * ((100vw - 768px) / (1920 - 768)));
      @media screen and (max-width: 767px) {
        margin-right: 0;
        width: 27px;
      }
    }
    @include Raleway;
    font-weight: 700;
    font-size: calc(10px + (16 - 10) * ((100vw - 768px) / (1920 - 768)));
    line-height: calc(12px + (19 - 12) * ((100vw - 768px) / (1920 - 768)));
    color: #fff;
    margin-right: calc(17px + (32 - 17) * ((100vw - 768px) / (1920 - 768)));
  }
  .dropdown {
    a {
      @include Raleway;
      font-weight: 700;
      font-size: calc(10px + (16 - 10) * ((100vw - 768px) / (1920 - 768)));
      line-height: calc(12px + (19 - 12) * ((100vw - 768px) / (1920 - 768)));
      color: #ffffff;
      &:hover {
        color: #fec87e;
        @include transition02;
      }
    }

    .dropdown-toggle::after {
      display: inline-block;
      margin-left: 0.255em;
      vertical-align: 0.255em;
      content: '';
      border-top: 0.3em solid;
      border-right: 0.3em solid transparent;
      border-bottom: 0;
      border-left: 0.3em solid transparent;
      color: #fec87e;
    }
    .dropdown-item {
      color: #000;
    }
  }
  &__logo {
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 373px;
    @media screen and (max-width: 991px) {
      max-width: 198px;
    }
  }
  &__btn_reg {
    @include Raleway;
    font-weight: 700;
    font-size: calc(10px + (18 - 10) * ((100vw - 768px) / (1920 - 768)));
    line-height: 1;
    text-transform: capitalize;
    color: #ffffff !important;
    border: 1px solid #ffffff;
    border-radius: 12px;
    padding: 10px 18px;
    margin-right: calc(14px + (23 - 14) * ((100vw - 768px) / (1920 - 768)));
    @media screen and (max-width: 991px) {
      padding: 8px 14px;
    }
    @media screen and (max-width: 767px) {
      font-size: 8px;
      padding: 5px 8px;
    }
    &:hover {
      opacity: 0.7;
    }
  }
  &__btn_log {
    @include Raleway;
    font-weight: 700;
    font-size: calc(10px + (18 - 10) * ((100vw - 768px) / (1920 - 768)));
    line-height: 1;
    text-transform: capitalize;
    color: #000;
    background: #fff279;
    border: 1px solid #2b67f6;
    border-radius: 12px;
    padding: 10px 32px;
    @media screen and (max-width: 991px) {
      padding: 8px 26px;
    }
    @media screen and (max-width: 767px) {
      background: #fff279;
      border-radius: 12px;
      font-size: 8px;
      color: #090000;
      padding: 5px 16px;
    }
    &:hover {
      opacity: 0.7;
    }
  }
  &__lk-avatar {
    width: calc(30px + (79 - 30) * ((100vw - 320px) / (1920 - 320)));
  }
}

.main-login {
  background-image: url(../img/bg-log.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.breadcrumb {
  background-image: url(../img/bg-breadcrumb.png);
  background-size: cover;
  background-position: center center;
  padding: 15px 0;
  @media screen and (min-width: 768px) {
    padding: 12px 0;
  }
  @media screen and (min-width: 1200px) {
    padding: 22px 0;
  }

  &__img {
    margin-right: 16px;
    @media screen and (max-width: 767px) {
      margin-right: 8px;
    }
  }

  &__link {
    @include Raleway;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    @include transition02;
    &:hover {
      color: #fec87e;
      @include transition02;
    }
    @media screen and (max-width: 767px) {
      font-size: 8px;
      line-height: 9px;
    }
  }
}

.main-login__form {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 12px;
  max-width: 750px;
  margin-top: 29px;
  padding: 82px 60px 22px;
  margin-bottom: 54px;
  @media screen and (max-width: 991px) {
    margin-bottom: 25px;
  }
  @media screen and (max-width: 767px) {
    padding: 16px;
    margin-bottom: 80px;
  }
  &_header {
    @include Raleway;
    margin-bottom: 65px;
    @media screen and (max-width: 767px) {
      margin-bottom: 10px;
    }
  }

  &_sigin {
    margin-bottom: 49px;
    @media screen and (max-width: 767px) {
      margin-bottom: 18px;
    }
  }

  &_title {
    font-weight: 700;
    font-size: 30px;
    line-height: 35px;
    text-transform: capitalize;
    color: #090000;
    margin-right: 120px;
    @media screen and (max-width: 767px) {
      font-size: 22px;
      line-height: 26px;
      margin-right: 32px;
    }
  }

  &_question {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #334b5f;
    @media screen and (max-width: 767px) {
      font-size: 8px;
      line-height: 9px;
    }
  }

  &_account {
    border-bottom: 1px solid #334b5f;
    padding-bottom: 4px;
    a {
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      color: #334b5f;
      @media screen and (max-width: 767px) {
        font-size: 8px;
        line-height: 9px;
      }
      span {
        text-decoration: underline;
      }
    }
  }

  &_body {
    margin-bottom: 64px;
    @include Raleway;
    @media screen and (max-width: 767px) {
      margin-bottom: 31px;
    }
  }

  &_input {
    position: relative;
    margin-bottom: 35px;
    @media screen and (max-width: 767px) {
      margin-bottom: 15px;
    }

    &::after {
      z-index: 2;
      display: block;
      position: absolute;
      content: '';
      top: 18px;
      left: 31px;
      background-image: url(../img/icons/pen.svg);
      width: 24px;
      height: 24px;
      background-size: 24px 24px;
      background-repeat: no-repeat;
      @media screen and (max-width: 767px) {
        top: 12px;
        left: 13px;
        width: 22px;
        height: 22px;
        background-size: 22px 22px;
      }
    }
    .form-control {
      background: #ffffff;
      display: block;
      border-color: #999999;
      padding: 20px 20px 20px 73px;
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      color: #334b5f;
      @media screen and (max-width: 767px) {
        padding: 16px 16px 16px 51px;
        font-size: 12px;
        line-height: 14px;
      }
      &::placeholder {
        font-weight: 400;
        font-size: 18px;
        line-height: 21px;
        color: #334b5f;
        @media screen and (max-width: 767px) {
          font-size: 12px;
          line-height: 14px;
        }
      }
    }
  }

  &_btn {
    margin-bottom: 16px;
    @media screen and (max-width: 767px) {
      margin-bottom: 20px;
    }
    .btn {
      border: 1px solid #353c3f;
      background: #353c3f;
      border-radius: 12px;
      font-weight: 700;
      font-size: 24px;
      line-height: 1;
      text-transform: capitalize;
      color: #ffffff;
      padding: 15px 40px;
      min-width: 280px;
      @include transition02;
      @media screen and (max-width: 767px) {
        border-radius: 6px;
        font-size: 8px;
        padding: 7px 23px;
        min-width: 115px;
      }
      &:hover {
        color: #353c3f;
        background-color: #fff;
      }
    }
  }

  .form-check {
    border-bottom: 1px solid #334b5f;
    padding-bottom: 8px;
    @media screen and (max-width: 767px) {
      padding-bottom: 6px;
    }
  }
  .form-check-input {
    width: 23px;
    height: 23px;
    border-color: #999999;
    margin-right: 21px;
  }
  .form-check-label {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #334b5f;
    @media screen and (max-width: 767px) {
      font-size: 10px;
      line-height: 12px;
    }
  }

  &_footer {
    p {
      font-family: 'Source Sans Pro', sans-serif;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      color: #334b5f;
      @media screen and (max-width: 767px) {
        font-size: 10px;
        line-height: 13px;
      }
    }
  }
}
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  a {
    @include transition02;
    &:hover {
      color: #fec87e;
      @include transition02;
    }
  }
  &__content {
    background: rgba(35, 42, 42, 0.85);
    padding-top: 76px;
    padding-bottom: 99px;
    @media screen and (max-width: 991px) {
      padding: 39px 0;
    }
    @media screen and (max-width: 767px) {
      padding-top: 29px;
      padding-bottom: 48px;
    }
  }

  &__logo {
    max-width: 206px;
    margin-bottom: 24px;
  }
  &__link {
    li {
      padding: 2px 0;
    }
    li:last-child {
      padding: 4px 0;
      position: relative;
      &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 40px;
        height: 2px;
        background-color: #fff;
      }
    }
  }
  &__contact {
    .footer__text {
      max-width: 163px;
    }
    &_icon {
      margin-right: 8px;
    }
    .footer__geo {
      margin-bottom: 11px;
    }
  }
  &__follow {
    .footer__text {
      max-width: 136px;
    }
  }

  &__text {
    @include Raleway;
    font-weight: 500;
    font-size: 16px;
    line-height: 23px;
    color: #ffffff;
    @media screen and (max-width: 991px) {
      font-size: 10px;
      line-height: 12px;
    }
  }

  &__title {
    display: block;
    @include Raleway;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.3;
    color: #ffffff;
    margin-bottom: 30px;
    @media screen and (max-width: 991px) {
      font-size: 12px;
      margin-bottom: 25px;
    }
    @media screen and (max-width: 767px) {
      margin-bottom: 16px;
    }
  }

  &__social {
    a {
      margin-right: 16px;
      &:last-child {
        margin-right: 0;
      }
      @media screen and (max-width: 991px) {
        margin-right: 12px;
      }
      &:hover {
        opacity: 0.5;
      }
    }
    &_fb {
      width: 12px;
      @media screen and (max-width: 991px) {
        width: 7px;
      }
    }

    &_inst {
      width: 23px;
      @media screen and (max-width: 991px) {
        width: 14px;
      }
    }

    &_twit {
      width: 26px;
      @media screen and (max-width: 991px) {
        width: 15px;
      }
    }

    &_whats {
      width: 23px;
      @media screen and (max-width: 991px) {
        width: 14px;
      }
    }
  }

  &__copyrights {
    padding: 26px 0;
    @media screen and (max-width: 991px) {
      padding: 16px 0;
    }
    @media screen and (max-width: 767px) {
      padding: 12px 0;
    }
    p {
      text-align: center;
      @include OpenSans;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #ffffff;
      @media screen and (max-width: 991px) {
        font-size: 10px;
        line-height: 22px;
      }
    }
  }
}
.map-home {
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 200px;
  &__bg {
    object-fit: cover;
    object-position: center center;
    height: calc(162px + (651 - 162) * ((100vw - 320px) / (1920 - 320)));
  }
  &__content {
    padding-top: calc(45px + (74 - 45) * ((100vw - 768px) / (1920 - 768)));
    max-width: calc(300px + (370 - 300) * ((100vw - 768px) / (1920 - 768)));
  }

  &__logo {
    margin-bottom: calc(13px + (16 - 13) * ((100vw - 768px) / (1920 - 768)));
    max-width: calc(140px + (264 - 140) * ((100vw - 768px) / (1920 - 768)));
  }

  &__desc {
    span {
      @include Redressed;
      font-weight: 400;
    }
    @include Raleway;
    font-weight: 700;
    font-size: calc(10px + (14 - 10) * ((100vw - 768px) / (1920 - 768)));
    line-height: calc(12px + (16 - 12) * ((100vw - 768px) / (1920 - 768)));
    color: #fff;
  }
}

.btn-close {
  position: absolute;
  opacity: 1;
  border-radius: 100%;
  top: 24px;
  right: 24px;
  width: 43px;
  height: 43px;
  background-image: url(../img/icons/icon-close.svg);
  background-size: 100% 100%;
  @media screen and (max-width: 767px) {
    top: 14px;
    right: 14px;
    width: 40px;
    height: 40px;
  }
}

.free-dates {
  &__wrapper {
    @include Raleway;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.6);
    min-height: 100vh;
  }

  &__popup {
    background: #ffffff;
    border-radius: 8px;
    max-width: 622px;
    margin-left: auto;
    margin-right: auto;
    padding: 20px calc(10px + (32 - 10) * ((100vw - 320px) / (1920 - 320)));
  }

  &__header {
    margin-bottom: calc(32px + (50 - 32) * ((100vw - 320px) / (1920 - 320)));
  }

  &__title {
    font-weight: 600;
    font-size: 36px;
    line-height: 44px;
    color: #000000;
    margin-bottom: 0;
  }

  &__body {
    padding-bottom: calc(28px + (40 - 28) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 1px solid #979797;
    margin-bottom: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
  }

  &__status {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin-bottom: 12px;
  }

  &__desc {
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: #000000;
    margin-bottom: 0;
  }

  &__footer {
  }

  &__close {
    font-weight: 400;
    font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1;
    text-decoration: underline;
    color: #235784;
    @include transition02;
    &:hover {
      @include transition02;
      text-decoration: none;
    }
  }

  &__save {
    @include Mulish;
    font-weight: 800;
    font-size: 16px;
    line-height: 1;
    text-align: center;
    color: #ffffff;
    border: 1px solid #a21818;
    background: #a21818;
    border-radius: 8px;
    padding: 12px 40px;
    @include transition02;
    &:hover {
      @include transition02;
      color: #a21818;
      background-color: #fff;
    }
  }
}

.form-check-input {
  width: calc(20px + (27 - 20) * ((100vw - 320px) / (1920 - 320)));
  height: calc(20px + (27 - 20) * ((100vw - 320px) / (1920 - 320)));
}
.form-check-input:checked {
  background-color: #fff;
  border-color: #235784;
}
.form-check-input:checked[type='radio'] {
  background-image: url(../img/icons/blob-haikei.svg);
}
.link-menu {
  color: #fff;
  @include transition02;
  &:hover {
    @include transition02;
    color: #fec87e;
  }
}
input,
textarea {
  cursor: pointer;
  &:hover,
  &:focus {
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
  }
}

.btn-red {
  background-color: #a21818;
  border: 1px solid #a21818;
  border-radius: 8px;
  width: 100%;
  @include Mulish;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  padding: 8px 30px;
  color: #ffffff;
  @include transition02;
  @media screen and (min-width: 768px) {
    width: 332px;
  }
  &:hover {
    @include transition02;
    color: #000;
    background-color: #fff;
  }
}

.btn-outline-red {
  background-color: #fff;
  border: 1px solid #a21818;
  border-radius: 8px;
  color: #000;
  width: 100%;
  @include Mulish;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  padding: 8px 30px;
  @include transition02;
  @media screen and (min-width: 768px) {
    width: 332px;
  }
  &:hover {
    color: #fff;
    @include transition02;
    background-color: #a21818;
  }
}

.form-input-blue {
  input[type='text'],
  input[type='password'],
  input[type='email'],
  input[type='tel'],
  textarea {
    padding: 2px 6px;
    @include Montserrat;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #484848;
    border: 1px solid rgba(35, 87, 132, 0.5);
    border-radius: 7px;
    height: 30px;
    display: block;
    width: 100%;
  }
  textarea {
    height: auto;
  }
}

.quantity-block {
  display: flex;
  align-items: center;
  padding: 3px 6px;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 7px;
  width: 110px;
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
  }
  input[type='number'] {
    -moz-appearance: textfield;
  }
}
.quantity-arrow-minus,
.quantity-arrow-plus {
  cursor: pointer;
  padding: 8px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15%;
  box-sizing: border-box;
  border-radius: 4px;
  outline: none;
}
.quantity-num {
  font-size: 20px;
  width: calc(70% - 8px);
  margin: 0 4px;
  border-radius: 4px;
  @include Montserrat;
  font-weight: 400;
  font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
  line-height: 1.4;
  text-align: center;
  color: #000000;
}

.btn-yellow {
  background: #fff279;
  border: 1px solid #fff279;
  border-radius: 8px;
  padding: 10px 20px;
  @include Mulish;
  font-weight: 800;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  width: 100%;
  max-width: 332px;
  @media screen and (max-width: 575px) {
    max-width: 100%;
  }
  &:hover {
    background: transparent;
  }
}

.block-404 {
  padding: calc(120px + (200 - 120) * ((100vw - 320px) / (1920 - 320))) 0;
  &__number {
    @include Mulish;
    font-weight: 400;
    font-size: calc(120px + (257 - 120) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.1;
    color: #fff279;
  }

  &__desc {
    @include Raleway;
    font-weight: 400;
    font-size: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: #ffffff;
    margin-bottom: calc(32px + (60 - 32) * ((100vw - 320px) / (1920 - 320)));
  }
}



@import '_landing';
@import '_lk';
@import '_stepper';