.room {
  border-radius: 12px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 20px;
}

.image {
  position: relative;
}

.action {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  z-index: 1;
  position: absolute;
  top: 10px;
  right: 10px;
  height: 40px;
  width: 40px;
  background: rgba(0, 0, 0, 0.5);
}

.price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 50px;
  bottom: 0;
  background: rgba(35, 87, 132, 0.5);
  padding: 0 20px;

  span {
    font-family: Poppins, sans-serif;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #E9E9E9;
  }
}

.indicators {
  display: flex;
}

.indicator {
  width: 10px;
  height: 10px;
  background: #FFFFFF;
  border-radius: 50%;
  margin-left: 5px;
  cursor: pointer;

  &[data-selected=true] {
    background: #235784;
  }
}

.information {
  padding: 5px 15px 10px 15px;

  button {
    margin-top: 10px;
    font-family: Poppins, sans-serif;
    font-weight: 600;
    height: 40px;
    width: 100%;
    background: #235784;
    border-radius: 8px;
    border: none;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;

    &[data-selected=true] {
      background: #A11818;
    }
  }

  table {
    margin-top: 5px;

    td {
      font-family: Poppins, sans-serif;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #9A9A9A;
    }

    .infoDescription {
      padding-left: 5px;
    }
  }
}

.title {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  font-family: Poppins, sans-serif;
  color: #484848;
}