.header {
  background-position: center;
}

.path {
  padding-top: 15px;
  background: rgba(247, 250, 253, 0.4);
  font-weight: 700;
  font-size: 18px;
  color: #000000;
  height: 60px;
}

/*Cover*/

.cover {
  border-radius: 20px;
  margin-bottom: 40px;
  margin-top: 20px;
}

.coverContent {
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 100px 75px !important;

  h1 {
    font-weight: 700;
    font-size: 60px;
    line-height: 80px;
    color: #FFFFFF;
    margin-bottom: 12px;
  }

  span {
    font-weight: 400;
    font-size: 24px;
    line-height: 40px;
    color: #FFFFFF;
  }
}

.coverContentActions {
  margin-top: 70px;
  display: flex;

  a {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 18px;
    color: #000000;
    background: #FFFFFF;
    border-radius: 16px;
    margin-right: 60px;
  }
}

.coverWatchVideo {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #FFFFFF;

  div {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #FFFFFF;
    height: 48px;
    width: 48px;
    border-radius: 50%;
    margin-right: 15px;
  }
}

/*Description*/

.description {
  font-family: Poppins, sans-serif;
  margin-bottom: 20px;
  padding: 20px 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 19px;
  color: #FFFFFF;
}

/*Search*/

.search {
  padding: 35px 0;
}

.searchDate {
  padding: 15px 30px;
  background: #FFFFFF;
  border-radius: 16px;
}

.searchDateTitle {
  font-family: Poppins, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #A1A1A2
}

.searchDatePicker {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
  font-size: 18px;
  line-height: 27px;
  color: #384652;
  cursor: pointer;

  div {
    font-family: 'Poppins', 'sans-serif';
    display: flex;
    align-items: center;

    img {
      margin-right: 8px;
    }
  }
}

.searchButton {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  width: 100%;
  height: 87px;
  background: #FFF279;
  border-radius: 16px;
  border: none
}

/*Filters*/

.filters {
  margin-top: 25px;
  font-family: Poppins, sans-serif;
  background: #e1e1e1;
  box-shadow: 0px 24px 50px rgba(0, 0, 0, 0.25);
  border-radius: 16px;
  padding: 10px 20px;
  display: flex;
  align-items: center;

  span {
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #000000;
  }
}

.filter {
  background: #FFFFFF;
  display: flex;
  justify-content: center;
  padding: 5px 15px;
  box-shadow: 2px 8px 43px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  color: #384652;
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  margin-left: 10px;
}

/*Chosen Rooms*/

.chosen_rooms, .available_rooms {
  margin-bottom: 45px;

  fieldset {
    margin: 50px 0 10px 0;
    font-weight: 800;
    font-size: 36px;
    line-height: 42px;
    padding-left: 0;
    border-left: none;
    border-right: none;
    border-bottom: none;
    border-top: 4px solid #000000;

    legend {
      padding-right: 25px;
    }
  }
}

.total_price {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  margin-bottom: 20px;

  span {
    font-weight: 600;
    font-size: 25px;
  }

  button {
    color: #ffffff;
  }
}

