@mixin Nunito {
  font-family: "Nunito Sans", sans-serif;
}
@mixin Raleway {
  font-family: "Raleway", sans-serif;
}
@mixin OpenSans {
  font-family: "Open Sans", sans-serif;
}
@mixin Mulish {
  font-family: 'Mulish', sans-serif;
}
@mixin Montserrat {
  font-family: 'Montserrat', sans-serif;
}
@mixin Manrope {
  font-family: 'Manrope', sans-serif;
}
@mixin Redressed {
  font-family: 'Redressed', cursive;
}
@mixin Lato {
  font-family: 'Lato', sans-serif;
}

.authorization {
  .form-input-blue {
    @media screen and (min-width: 768px) {
      width: 65%;
    }
  }
  max-width: 600px;
  margin: calc(32px + (50 - 32) * ((100vw - 320px) / (1920 - 320))) auto;
  padding-top: calc(20px + (26 - 20) * ((100vw - 320px) / (1920 - 320)));
  padding-right: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
  padding-bottom: calc(32px + (47 - 32) * ((100vw - 320px) / (1920 - 320)));
  padding-left: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    padding-bottom: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
  }

  &__title {
    @include Montserrat;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #484848;
  }

  &__reg {
    @include Raleway;
    font-style: normal;
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 44px;
    text-decoration-line: underline;
    color: #235784;
  }

  &__form {
    &_group {
      display: flex;
      flex-direction: column;
      margin-bottom: 16px;
      @media screen and (min-width: 768px) {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      }
    }
  }

  &__label {
    @include Montserrat;
    font-weight: 500;
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 20px;
    color: #484848;
    margin-bottom: 8px;
    width: 100%;
    @media screen and (min-width: 768px) {
      width: 35%;
      margin-bottom: 0;
      padding-right: 24px;
    }
  }

  &__forgot {
    margin-top: 8px;
    @include Nunito;
    font-weight: 400;
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 22px;
    text-decoration-line: underline;
    color: #235784;
  }

  &__remember {
    margin-top: 8px;
    @include Nunito;
    font-weight: 400;
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 22px;
    color: #000000;
    .form-check-input:checked {
      background-color: #235784;
      border-color: #235784;
    }
    .form-check-input {
      margin-right: 8px;
      width: 20px;
      height: 20px;
    }
  }

  &__submit {
    margin-top: calc(32px + (76 - 32) * ((100vw - 320px) / (1920 - 320)));
  }
}

.reg-object {
  &__desc {
    font-weight: 400;
    @include Raleway;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 144%;
    color: #000000;
    margin-bottom: 24px;
  }

  &__card {
    padding-top: calc(24px + (32 - 24) * ((100vw - 320px) / (1920 - 320)));
    padding-bottom: calc(28px + (38 - 28) * ((100vw - 320px) / (1920 - 320)));
    padding-right: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    padding-left: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    background: #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    @include Montserrat;
    &_img {
      margin-bottom: calc(24px + (40 - 24) * ((100vw - 320px) / (1920 - 320)));
      img {
        width: 100%;
        max-width: 134px;
      }
    }

    &_title {
      margin-bottom: 12px;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: #484848;
    }

    &_desc {
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #9a9a9a;
      margin-bottom: calc(24px + (32 - 24) * ((100vw - 320px) / (1920 - 320)));
    }

    &_btn {
      .btn-red {
        width: 100%;
      }
    }
  }
}

.stepper__tabs {
  display: flex;
  flex-wrap: nowrap;
  max-width: 100%;
  overflow-y: hidden;
  overflow-x: auto;
  padding-bottom: calc(18px + (28 - 18) * ((100vw - 320px) / (1920 - 320)));
  margin-bottom: 6px;
  &_item {
    position: relative;
    white-space: nowrap;
    cursor: pointer;
    font-weight: 400;
    @include Nunito;
    padding-right: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
    padding-left: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
    padding-bottom: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320)));
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #000000;
    margin-right: calc(12px + (21 - 12) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 2px solid #979797;
    &.active {
      border-bottom: 2px solid #235784;
    }
    &.active-50 {
      position: relative;
      &::after {
        position: absolute;
        content: '';
        bottom: -2px;
        left: 0;
        width: 50%;
        height: 2px;
        background-color: #235784;
      }
    }
  }
}

.stepper-address {
  &__desc {
    @include Raleway;
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    margin-bottom: calc(20px + (32 - 20) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
  }
  &__form {
    @include Montserrat;
    max-width: 431px;
    background: #ffffff;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: calc(24px + (32 - 24) * ((100vw - 320px) / (1920 - 320)))
      calc(16px + (32 - 16) * ((100vw - 320px) / (1920 - 320)));
    &_card {
    }

    &_group {
      margin-bottom: calc(12px + (20 - 12) * ((100vw - 320px) / (1920 - 320)));
    }

    &_label {
      font-weight: 700;
      font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
      line-height: 22px;
      color: #484848;
      margin-bottom: calc(8px + (12 - 8) * ((100vw - 320px) / (1920 - 320)));
    }

    &_btn {
      text-align: center;
    }
  }
  .form-input-blue {
    width: 100%;
  }
  .btn-red {
    width: 100%;
    max-width: 332px;
  }
}

.stepper-dop-info {
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  padding-top: calc(30px + (24 - 30) * ((100vw - 320px) / (1920 - 320)));
  padding-bottom: calc(30px + (48 - 30) * ((100vw - 320px) / (1920 - 320)));
  padding-left: calc(12px + (26 - 12) * ((100vw - 320px) / (1920 - 320)));
  padding-right: calc(12px + (26 - 12) * ((100vw - 320px) / (1920 - 320)));
  .form-check-input:checked {
    background-color: #235784;
    border-color: #235784;
  }
  .form-check-input.form-check-radio:checked {
    background-color: #fff;
    border-color: #235784;
  }

  .form-check-label {
    font-weight: 400;
    @include Nunito;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1;
    color: #000000;
  }
  .form-check-input {
    margin-right: 8px;
    width: 20px;
    height: 20px;
  }
  &__check-group {
    padding-bottom: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 1px solid rgba(0, 0, 0, 0.3);
    &:last-child {
      border-bottom: none;
    }
  }
  &__desc {
    @include Raleway;
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: #000000;
    margin-bottom: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    &_green {
      max-width: 484px;
      color: #3d9e44;
      opacity: 0.6;
    }
    &_bg-green {
      background: rgba(190, 222, 192, 0.48);
      padding: calc(16px + (28 - 16) * ((100vw - 320px) / (1920 - 320)));
    }
  }

  &__profit {
    &_percent {
      @include Raleway;
      font-weight: 400;
      font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.5;
      color: #000000;
      opacity: 0.6;
    }

    &_title {
      @include Montserrat;
      font-weight: 500;
      font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.5;
      color: #484848;
    }

    &_list-item {
      @include Raleway;
      font-weight: 400;
      font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.4;
      color: #000000;
    }
    &_total {
      @include Montserrat;
      font-weight: 500;
      font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.4;
      color: #484848;
      span {
        @include Raleway;
        font-weight: 400;
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
        line-height: 1.4;
        color: #000000;
        opacity: 0.6;
      }
    }
  }

  &__form {
    &_group {
    }

    &_label {
      @include Montserrat;
      font-weight: 700;
      font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.4;
      margin-bottom: calc(6px + (9 - 6) * ((100vw - 320px) / (1920 - 320)));
      color: #484848;
    }
  }

  &__add-btn {
    @include Montserrat;
    font-weight: 700;
    font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
    line-height: 38px;
    height: 38px;
    padding: 0 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #484848;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 7px;
    white-space: nowrap;
    margin-bottom: 12px;
    cursor: pointer;
    @media screen and (min-width: 576px) {
      margin-bottom: 0;
    }
    &:hover {
      opacity: 0.6;
    }
    img {
      margin-left: 12px;
    }
  }

  &__add-counter {
    @include Raleway;
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: #000000;
    opacity: 0.6;
    padding-left: calc(12px + (24 - 12) * ((100vw - 320px) / (1920 - 320)));
  }
  &__preview {
    @include Montserrat;
    font-weight: 700;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    text-decoration-line: underline;
    color: #235784;
    cursor: pointer;
  }
  .input-group {
    max-width: 415px;
    .form-control {
      padding: 2px 6px;
      @include Montserrat;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: #484848;
      border: 1px solid rgba(0, 0, 0, 0.5);
      border-radius: 7px;
      height: 33px;
      display: block;
    }
    .input-group-text {
      display: flex;
      align-items: center;
      padding: 0.375rem 0.75rem;
      font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1;
      color: rgba(0, 0, 0, 0.6);
      font-weight: 400;
      text-align: center;
      white-space: nowrap;
      border: 1px solid rgba(0, 0, 0, 0.5);
      background-color: #fff;
      border-radius: 11px;
      height: 33px;
    }
    .input-group-text.input-group-text-blue {
      color: #fff;
      background: #235784;
      border: 1px solid #235784;
    }
  }
  &__form_desc {
    @include Raleway;
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: #000000;
    opacity: 0.6;
  }
  .border-bottom {
    border-bottom: 1px solid rgba(0, 0, 0, 0.3) !important;
  }
}
.form-input-blue {
  input {
    max-width: 415px;
  }
  textarea {
    max-width: 415px;
  }
}
.question-info {
  position: relative;
  &:hover .question-info-dop {
    display: block;
  }
}
.question-info-dop {
  position: absolute;
  z-index: 2;
  display: none;
  top: 0;
  left: 16px;
  width: 200px;
  background-color: #fff;
  border-radius: 8px;
  @include Raleway;
  font-weight: 400;
  font-size: calc(12px + (14 - 12) * ((100vw - 320px) / (1920 - 320)));
  line-height: 1.2;
  color: rgba(0, 0, 0, 0.6);
  padding: 4px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  @media screen and (max-width: 575px) {
    left: auto;
    right: 16px;
    width: 140px;
  }
}

.done-object {
  margin-top: calc(40px + (80 - 40) * ((100vw - 320px) / (1920 - 320)));
  padding: calc(24px + (50 - 24) * ((100vw - 320px) / (1920 - 320)))
    calc(16px + (108 - 16) * ((100vw - 320px) / (1920 - 320)));
  max-width: 754px;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  &__title {
    @include Montserrat;
    font-weight: 700;
    font-size: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: calc(20px + (28 - 20) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.5;
    color: #484848;
  }

  &__desc {
    @include Raleway;
    margin-bottom: calc(24px + (36 - 24) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 400;
    font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: #000000;
  }
}
