.button {
  font-family: Poppins, sans-serif;
  padding: 10px 20px;
  background: #FFF279;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  border: none;
}

.outlined_button {
  font-family: Poppins, sans-serif;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  border-radius: 10px;
  border: 1px #ffffff solid;
  background: transparent;
}