@mixin Nunito {
  font-family: "Nunito Sans", sans-serif;
}
@mixin Raleway {
  font-family: "Raleway", sans-serif;
}
@mixin OpenSans {
  font-family: "Open Sans", sans-serif;
}
@mixin Mulish {
  font-family: 'Mulish', sans-serif;
}
@mixin Montserrat {
  font-family: 'Montserrat', sans-serif;
}
@mixin Manrope {
  font-family: 'Manrope', sans-serif;
}
@mixin Redressed {
  font-family: 'Redressed', cursive;
}
@mixin Lato {
  font-family: 'Lato', sans-serif;
}

@mixin transition02 {
  transition: 0.2s;
}


.wrapper-lk {
  .container {
    max-width: 1620px;
  }
}
.main-lk {
  background-image: url(../img/landing/bg-main.png);
  background-repeat: round;
  &__content {
    margin-top: calc(24px + (32 - 24) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: 60px;
  }
}

.title-lk {
  @include Raleway;
  font-weight: 600;
  font-size: calc(22px + (30 - 22) * ((100vw - 320px) / (1920 - 320)));
  line-height: 147%;
  color: #000000;
  margin-bottom: 24px;
  @media screen and (min-width: 768px) {
    margin-bottom: 55px;
  }
  @media screen and (min-width: 992px) {
    margin-bottom: 45px;
  }
}
.card-user-lk {
  padding: calc(15px + (46 - 15) * ((100vw - 320px) / (1920 - 320)));
  background: #ffffff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  @include Nunito;
  @include transition02;
  &:hover {
    @include transition02;
    box-shadow: 0 0 12px rgba(0, 0, 0, 0.15);
  }
  &__icon {
    max-width: calc(45px + (80 - 45) * ((100vw - 320px) / (1920 - 320)));
    margin-right: calc(16px + (26 - 16) * ((100vw - 320px) / (1920 - 320)));
  }
  &__title {
    font-weight: 400;
    margin-bottom: 2px;
    font-size: calc(14px + (19 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(19px + (27 - 19) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
  }
  &__desc {
    font-weight: 400;
    font-size: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(14px + (22 - 14) * ((100vw - 320px) / (1920 - 320)));
    color: rgba(0, 0, 0, 0.6);
  }
}

.lk-dropdown-menu {
  @include Nunito;
  background: #ffffff;
  padding: 0 8px;
  border: 1px solid #979797;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  &__item {
    border-bottom: 1px solid #979797;
    padding: 16px 0;
    @include transition02;
    &:hover {
      @include transition02;
      .lk-dropdown-menu__item_title {
        color: #235784;
      }
    }
    &:last-child {
      border-bottom: 0;
    }
    &_icon {
      max-width: 33px;
      min-width: 33px;
      margin-right: 21px;
    }

    &_title {
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #000000;
    }
  }
}
.lk-form-user-info {
  max-width: 512px;
  padding: calc(16px + (42 - 16) * ((100vw - 320px) / (1920 - 320)))
    calc(20px + (50 - 20) * ((100vw - 320px) / (1920 - 320)));
  background: #ffffff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  border-radius: 10px;
  &__avatar {
    max-width: calc(55px + (109 - 55) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: 24px;
  }

  &__item {
    margin-bottom: 12px;
    @include Montserrat;
    &_header {
      margin-bottom: 14px;
    }
    &_label {
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: #484848;
    }
    &_edit {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      text-decoration-line: underline;
      color: #235784;
    }

    &_input {
      border: 1px solid rgba(0, 0, 0, 0.5);
      border-radius: 7px;
      height: 30px;
      line-height: 30px;
      padding: 0 8px;
      @include transition02;
      &:hover {
        @include transition02;
        border-color: #235784;
      }
    }
  }
}

.lk-card-apartment {
  background: #ffffff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  border-radius: 10px;
  padding-top: calc(25px + (36 - 25) * ((100vw - 320px) / (1920 - 320)));
  padding-bottom: calc(36px + (26 - 36) * ((100vw - 320px) / (1920 - 320)));
  padding-left: calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)));
  padding-right: calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)));
  &__header {
    padding-bottom: calc(38px + (30 - 38) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 1px solid #979797;
    margin-bottom: calc(16px + (38 - 16) * ((100vw - 320px) / (1920 - 320)));
  }

  &__img {
    max-width: 172px;
    min-width: 172px;
    @media screen and (max-width: 767px) {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 16px;
    }
    @media screen and (min-width: 768px) {
      margin-right: 32px;
    }
    img {
      border-radius: 12px;
      height: 164px;
      object-fit: cover;
      object-position: center center;
    }
  }

  &__title {
    @include Montserrat;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #484848;
    margin-bottom: 12px;
    @media screen and (max-width: 767px) {
      margin-bottom: 32px;
    }
  }

  &__list {
    margin-bottom: calc(24px + (32 - 24) * ((100vw - 320px) / (1920 - 320)));
    @media screen and (max-width: 767px) {
      max-width: 190px;
      margin-left: auto;
      margin-right: auto;
    }
    &_item {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
      p {
        @include Montserrat;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #9a9a9a;
      }
    }
    &_img {
      text-align: center;
      min-width: 29px;
      max-width: 29px;
      margin-right: 8px;
    }
  }

  &__btn {
    @include Mulish;
    background: #a21818;
    border-radius: 8px;
    font-weight: 800;
    font-size: 16px;
    line-height: 40px;
    height: 40px;
    padding: 0 76px;
    color: #ffffff;
  }

  &__info {
    @media screen and (min-width: 768px) {
      max-height: 140px;
    }
    &_item {
      margin-bottom: calc(8px + (16 - 8) * ((100vw - 320px) / (1920 - 320)));
      @include Nunito;
    }

    &_title {
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: rgba(0, 0, 0, 0.6);
      margin-bottom: 0;
      @media screen and (max-width: 767px) {
        margin-right: 10px;
      }
    }

    &_bold {
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      color: #000000;
      margin-bottom: 0;
    }

    &_normal {
      font-weight: 400;
      font-size: 14px;
      line-height: 19px;
      color: #000000;
      margin-bottom: 0;
    }
  }
}

.lk-card-edit-booking {
  padding: calc(32px + (36 - 32) * ((100vw - 320px) / (1920 - 320)))
    calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
  background: #ffffff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  border-radius: 10px;
  &__content {
    max-width: 350px;
    @media screen and (max-width: 1199px) {
      margin-left: auto;
      margin-right: auto;
    }
  }
  &__title {
    @include Montserrat;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #484848;
    margin-bottom: 16px;
  }

  &__btn {
    @include Mulish;
    border: 1px solid #000000;
    border-radius: 8px;
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    font-size: 16px;
    color: #000000;
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
    &:hover {
      background: #484848;
      border-color: #484848;
      color: #fff;
    }
  }
}

.lk-card-support {
  max-width: 710px;
  @media screen and (max-width: 991px) {
    max-width: 478px;
    margin-left: auto;
    margin-right: auto;
  }
  background: #ffffff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  border-radius: 10px;
  padding: 27px calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)))
    calc(60px + (40 - 60) * ((100vw - 320px) / (1920 - 320)));
  &__title {
    @include Montserrat;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #484848;
    margin-bottom: 0;
    @media screen and (max-width: 767px) {
      margin-bottom: 7px;
    }
  }

  &__desc {
    @include Mulish;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: calc(32px + (16 - 32) * ((100vw - 320px) / (1920 - 320)));
  }

  &__form {
    @include OpenSans;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #000000;
    max-width: 415px;
    margin-bottom: 36px;
    &_textarea {
      border: 1px solid rgba(0, 0, 0, 0.5);
      border-radius: 7px;
      padding: 8px;
      margin-bottom: 16px;
    }

    &_submit {
      @include Mulish;
      max-width: 246px;
      background: #a21818;
      border-radius: 8px;
      font-weight: 800;
      font-size: 16px;
      line-height: 40px;
      height: 40px;
      padding: 0 16px;
      color: #ffffff;
      @media screen and (max-width: 991px) {
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  &__info {
    &_title {
      @include Montserrat;
      font-weight: 700;
      font-size: 18px;
      line-height: 22px;
      color: #484848;
      margin-bottom: 24px;
    }

    &_tel {
      @include OpenSans;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #000000;
      @media screen and (max-width: 767px) {
        margin-bottom: 24px;
      }
      @media screen and (min-width: 768px) {
        margin-right: 32px;
      }
      @media screen and (min-width: 1200px) {
        margin-right: 70px;
      }
    }

    &_email {
      @include OpenSans;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      color: #000000;
    }
  }
}
.admin-indicators {
  height: 100%;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  @include Manrope;
  padding: 10px 8px 10px;
  @media screen and (min-width: 768px) {
    padding: 10px 16px 32px;
  }
  @media screen and (min-width: 1200px) {
    padding: 18px 10px 18px 24px;
  }
  &__row {
    @media screen and (max-width: 1199px) {
      &.row {
        margin-left: -4px;
        margin-right: -4px;
        .col-6 {
          padding: 0 4px;
        }
      }
    }
  }
  &__percent {
    font-weight: 700;
    font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(27px + (32 - 27) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
    margin-bottom: 5px;
  }

  &__desc {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.6);
  }

  &__img {
    min-width: 77px;
    max-width: 77px;
    @media screen and (max-width: 1199px) {
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 16px;
    }
  }
}
.admin-booking {
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  padding: 24px 54px 80px 24px;
  @media screen and (max-width: 767px) {
    padding: 16px 8px;
  }
  &__title {
    @include Raleway;
    font-weight: 600;
    font-size: 24px;
    line-height: 28px;
    color: #000000;
    margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
  }

  &__city {
    @include Raleway;
    font-weight: 600;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
  }

  &__date {
    @include Montserrat;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.6);
    margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
  }

  &__info {
    margin-bottom: calc(8px + (32 - 8) * ((100vw - 320px) / (1920 - 320)));
    @media screen and (max-width: 767px) {
      border-radius: 8px;
      background-color: #fff;
      box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
      padding: 16px 6px 24px;
    }
  }
  &__card {
    @media screen and (min-width: 768px) {
      border-radius: 8px;
      background-color: #fff;
      box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
      padding: 24px;
    }
    &_img {
      min-width: 94px;
      max-width: 94px;
      margin-right: calc(7px + (24 - 7) * ((100vw - 320px) / (1920 - 320)));
    }

    &_title {
      @include Raleway;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      color: #000000;
      margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
    }

    &_date {
      @include Montserrat;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: rgba(0, 0, 0, 0.6);
      margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
    }

    &_price {
      @include Montserrat;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      color: #000000;
      margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
      @media screen and (max-width: 767px) {
        width: 100%;
      }
      @media screen and (min-width: 768px) {
        position: absolute;
        top: 24px;
        right: 24px;
      }
    }

    &_edit {
    }

    &_status {
      @include Montserrat;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: #a21818;
      &.active {
        color: #219478;
      }
    }
  }
}

.admin-objects {
  @media screen and (min-width: 768px) {
    padding: 16px 10px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  }
  @media screen and (min-width: 1200px) {
    padding: 26px;
  }
  &__hotel {
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
    padding: 24px;
    @media screen and (max-width: 767px) {
      padding: 28px 8px 12px;
    }
    &_add-edit {
      position: absolute;
      top: 0;
      right: 0;
      padding: 18px 10px;
      @media screen and (max-width: 767px) {
        padding: 4px;
      }
      a {
        @include Montserrat;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-decoration: underline;
        color: #235784;
        @include transition02;
        &:hover {
          @include transition02;
          text-decoration: none;
        }
      }
    }

    &_img {
      min-width: calc(64px + (94 - 64) * ((100vw - 320px) / (1920 - 320)));
      max-width: calc(64px + (94 - 64) * ((100vw - 320px) / (1920 - 320)));
      margin-right: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    }

    &__header {
      margin-bottom: calc(8px + (12 - 8) * ((100vw - 320px) / (1920 - 320)));
    }
    &_title {
      @include Raleway;
      font-weight: 600;
      font-size: 18px;
      line-height: 20px;
      color: #000000;
      margin-bottom: 0;
      margin-right: calc(4px + (12 - 4) * ((100vw - 320px) / (1920 - 320)));
    }

    &_rating {
      img {
        width: 16px;
        margin-right: 2px;
      }
    }

    &_review {
      @include Montserrat;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: rgba(0, 0, 0, 0.6);
      margin-bottom: calc(8px + (12 - 8) * ((100vw - 320px) / (1920 - 320)));
    }

    &_status {
      @include Montserrat;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: #a21818;
      margin-bottom: 0;
      &.active {
        color: #219478;
      }
      @media screen and (max-width: 767px) {
        margin-bottom: 12px;
      }
    }

    &_list {
      @include Mulish;
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;
      color: #235784 !important;
      @include transition02;
      @media screen and (min-width: 768px) {
        position: absolute;
        bottom: 0;
        right: 0;
        padding: 20px 10px;
      }
      &.active,
      &:hover {
        img {
          @include transition02;
          transform: rotate(180deg);
        }
      }
      &:hover {
        @include transition02;
        text-decoration: underline;
      }
      img {
        @include transition02;
      }
    }
  }
}

.admin-objects {
  &__rooms {
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
    &_wrapper {
      @media screen and (min-width: 992px) {
        padding: 16px 24px;
        border-radius: 8px;
        background-color: #fff;
        box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
      }
      .row {
        margin-left: -4px;
        margin-right: -4px;
        .col-12 {
          padding-left: 4px;
          padding-right: 4px;
        }
      }
    }

    &_img {
      border-radius: 12px 12px 0 0;
    }
    &_photo {
      border-radius: 12px 12px 0 0;
      height: 200px;
      object-fit: cover;
      object-position: center center;
    }

    &_edit {
      background-color: transparent;
      padding: 0;
      border: none;
      margin: calc(12px + (8 - 12) * ((100vw - 320px) / (1920 - 320)));
      @include transition02;
      &:hover {
        @include transition02;
        opacity: 0.7;
      }
    }

    &_price {
      @include Montserrat;
      font-weight: 600;
      font-size: 18px;
      line-height: 22px;
      color: #e9e9e9;
      margin-bottom: 5px;
      margin-left: 22px;
    }

    &_body {
      padding: 18px;
    }

    &_dots {
      margin: 10px;
      li {
        background-color: #fff;
        border-radius: 100%;
        width: 10px;
        height: 10px;
        margin-left: 5px;
        cursor: pointer;
        @include transition02;
        &.active,
        &:hover {
          @include transition02;
          background-color: #235784;
        }
      }
    }

    &_title {
      @include Montserrat;
      font-weight: 700;
      font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      line-height: calc(20px + (22 - 20) * ((100vw - 320px) / (1920 - 320)));
      color: #484848;
      margin-bottom: calc(20px + (8 - 20) * ((100vw - 320px) / (1920 - 320)));
    }

    &_info {
      @include Montserrat;
      font-weight: 500;
      font-size: 14px;
      line-height: 17px;
      color: #9a9a9a;
      margin-bottom: calc(4px + (8 - 4) * ((100vw - 320px) / (1920 - 320)));
      &:last-child {
        margin-bottom: 0;
      }
      &_img {
        min-width: 30px;
        margin-right: 8px;
      }
    }
  }
}

.admin-callback-content {
  @media screen and (min-width: 992px) {
    padding: 24px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  }
}
.admin-callback-cart {
  margin-bottom: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
  @include Nunito;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  padding: 12px;
  @media screen and (min-width: 768px) {
    padding: 20px;
    padding-left: 100px;
  }
  &__header {
    margin-bottom: 17px;
    @media screen and (min-width: 768px) {
      margin-bottom: 12px;
    }
  }
  &__date {
    font-weight: 400;
    font-size: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
    color: rgba(0, 0, 0, 0.6);
    @media screen and (min-width: 992px) {
      position: absolute;
      top: 12px;
      right: 12px;
    }
  }

  &__title {
    @include Raleway;
    font-weight: 600;
    font-size: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(20px + (26 - 20) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
  }

  &__avatar {
    max-width: 40px;
    @media screen and (max-width: 767px) {
      margin-right: 8px;
    }
    @media screen and (min-width: 768px) {
      max-width: 70px;
      position: absolute;
      top: 16px;
      left: 16px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(19px + (22 - 19) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
    margin-bottom: calc(6px + (12 - 6) * ((100vw - 320px) / (1920 - 320)));
    &:last-child {
      margin-bottom: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
    }
  }

  &__status {
    font-weight: 400;
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(19px + (22 - 19) * ((100vw - 320px) / (1920 - 320)));
    text-decoration: underline;
    color: #235784;
    &.active {
      text-decoration: none;
      color: #219478;
    }
  }
}

.admin-calendar-main {
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  padding: 24px 24px 40px 40px;
  @include Nunito;
  @media screen and (max-width: 767px) {
    padding: 30px 10px;
  }
  &__tab {
    margin-bottom: 35px;
    &_item {
      cursor: pointer;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      text-align: center;
      color: #000000;
      margin-right: 23px;
      padding: 0 40px 16px 40px;
      border-bottom: 2px solid #979797;
      &.active,
      &:hover {
        border-bottom: 2px solid #235784;
      }
    }
  }

  &__select {
    cursor: pointer;
    @include Nunito;
    border: 1px solid #235784;
    max-width: 192px;
    padding: 4px 30px 4px 12px;
    border-radius: 7px;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: rgba(0, 0, 0, 0.6);
    appearance: none;
    background-color: #fff;
    background-image: url("../img/icons/arrow-down-blue.svg");
    background-repeat: no-repeat, repeat;
    background-position: right 0.7em top 50%, 0 0;
    background-size: 0.65em auto, 100%;
    margin-bottom: 40px;
    @media screen and (max-width: 767px) {
      margin-bottom: 5px;
      margin-left: auto;
      margin-right: auto;
      &:last-child {
        margin-bottom: 26px;
      }
    }
    &::-ms-expand {
      display: none;
    }
    &:hover {
      box-shadow: 0 0 0 1px #235784;
    }
    &:focus {
      box-shadow: 0 0 0 1px #235784;
      outline: none;
    }
  }

  &__desc {
    font-weight: 400;
    font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(22px + (25 - 22) * ((100vw - 320px) / (1920 - 320)));
    color: #000000;
    margin-bottom: 23px;
  }

  &__date {
    @include Lato;
    font-weight: 700;
    font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
    line-height: calc(19px + (24 - 19) * ((100vw - 320px) / (1920 - 320)));
    color: #828282;
    padding: calc(8px + (16 - 8) * ((100vw - 320px) / (1920 - 320))) 0;
    img {
      width: calc(14px + (24 - 14) * ((100vw - 320px) / (1920 - 320)));
    }
  }
}

.admin-calendar-side {
  position: relative;
  @include Raleway;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0px 4px 18px rgba(89, 89, 89, 0.08);
  padding: 24px 12px;
  color: #000000;
  @media screen and (max-width: 767px) {
    padding: 48px 16px;
  }

  &__title {
    font-weight: 600;
    font-size: 30px;
    line-height: 34px;
    margin-bottom: 27px;
  }

  &__status {
    font-weight: 600;
    font-size: 22px;
    line-height: 26px;

    &_edit {
      font-weight: 400;
      font-size: 16px;
      line-height: 44px;
      text-decoration: underline;
      color: #235784;
    }
  }

  &__configure {
    font-weight: 400;
    font-size: 18px;
    line-height: 44px;
    padding-bottom: 20px;
    border-bottom: 1px solid #979797;
    margin-bottom: 20px;
  }

  &__price {
    margin-bottom: 20px;
  }
  &__input-title {
    font-weight: 600;
    font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
    line-height: 24px;
    margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));
  }

  &__input {
    position: relative;
    input {
      cursor: pointer;
      width: 100%;
      display: block;
      border: 1px solid rgba(0, 0, 0, 0.6);
      border-radius: 7px;
      font-weight: 400;
      font-size: 20px;
      line-height: 1;
      padding: 4px 12px;
    }
  }

  &__input-edit {
    position: absolute;
    top: 7px;
    right: 7px;
    background-image: url(../img/icons/icon-edit.svg);
    background-size: contain;
    background-repeat: no-repeat;
    width: 17px;
    height: 17px;
    z-index: 2;
    cursor: pointer;
  }
  &__sale {
  }
  &__btn {
    &_item {
      max-width: 385px;
      margin-left: auto;
      margin-right: auto;
      @include Raleway;
      width: 100%;
      display: block;
      padding: 12px;
      border: 1px solid #235784;
      border-radius: 7px;
      font-weight: 500;
      font-size: 18px;
      line-height: 1;
      text-align: center;
      color: #000000;
      margin-bottom: 8px;
      @include transition02;
      &:hover {
        @include transition02;
        background-color: #235784;
        color: #fff;
      }
    }
  }
}

