@mixin Nunito {
  font-family: "Nunito Sans", sans-serif;
}

@mixin Raleway {
  font-family: "Raleway", sans-serif;
}

@mixin OpenSans {
  font-family: "Open Sans", sans-serif;
}

@mixin Mulish {
  font-family: 'Mulish', sans-serif;
}

@mixin Montserrat {
  font-family: 'Montserrat', sans-serif;
}

@mixin Manrope {
  font-family: 'Manrope', sans-serif;
}

@mixin Redressed {
  font-family: 'Redressed', cursive;
}

@mixin Lato {
  font-family: 'Lato', sans-serif;
}

@mixin transition02 {
  transition: 0.2s;
}


.main-landing {
  background-image: url(../img/landing/bg-main.png);
  background-repeat: round;

  &__heading {
    @include Raleway;

    &_span {
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      color: #040000;
      @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 16px;
      }
      @media screen and (min-width: 992px) {
        font-size: 16px;
        line-height: 19px;
      }
    }

    &_title {
      font-weight: 800;
      font-size: 22px;
      line-height: 1;
      color: #000000;
      margin-right: 24px;
      @media screen and (min-width: 992px) {
        font-size: 36px;
      }
    }

    hr {
      background: #000;
      height: 2px;
      @media screen and (min-width: 768px) {
        height: 4px;
      }
    }
  }

  .banner {
    background-image: url(../img/landing/bg-banner.png);
    background-size: cover;
    background-position: center center;
    border-radius: 20px;
    padding: 30px 18px;
    margin-bottom: 36px;
    @media screen and (min-width: 768px) {
      margin-bottom: 40px;
      padding: 70px 42px;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 55px;
      padding: 50px 74px 92px;
    }

    &__title {
      @include Raleway;
      font-weight: 700;
      font-size: 20px;
      line-height: 22px;
      color: #ffffff;
      margin-bottom: 16px;
      @media screen and (min-width: 768px) {
        font-size: 40px;
        line-height: 46px;
        margin-bottom: 24px;
      }
      @media screen and (min-width: 992px) {
        font-size: 60px;
        line-height: 80px;
        margin-bottom: 32px;
      }
    }

    &__desc {
      @include Raleway;
      font-weight: 400;
      color: #ffffff;
      font-size: 10px;
      line-height: 14px;
      max-width: 70%;
      margin-bottom: 16px;
      @media screen and (min-width: 768px) {
        font-size: 16px;
        line-height: 19px;
        max-width: 60%;
        margin-bottom: 32px;
      }
      @media screen and (min-width: 992px) {
        max-width: 55%;
        font-size: 24px;
        line-height: 40px;
        margin-bottom: 50px;
      }
    }

    .btn {
      @include Raleway;
      border-radius: 6px;
      padding-top: 4px;
      padding-bottom: 4px;
      font-weight: 400;
      font-size: 10px;
      line-height: 1;
      color: #000000;
      margin-bottom: 30px;
      @media screen and (min-width: 768px) {
        margin-bottom: 0;
        font-size: 16px;
        border-radius: 16px;
        padding-top: 12px;
        padding-bottom: 12px;
      }
      @media screen and (min-width: 992px) {
        font-size: 18px;
        padding-top: 14px;
        padding-bottom: 14px;
      }
    }

    &__btn-white {
      background: #ffffff;

      &:hover {
        background: #fff279;
      }

      padding-left: 8px;
      padding-right: 8px;
      margin-right: 10px;
      @media screen and (min-width: 768px) {
        padding-left: 25px;
        padding-right: 25px;
      }
    }

    &__btn-yellow {
      background: #fff279;

      &:hover {
        background: #ffffff;
      }

      padding-left: 22px;
      padding-right: 22px;
      @media screen and (min-width: 768px) {
        padding-left: 25px;
        padding-right: 25px;
      }
    }

    &__other {
      @media screen and (min-width: 768px) {
        position: absolute;
        bottom: 70px;
        right: 42px;
      }
      @media screen and (min-width: 992px) {
        bottom: 92px;
        right: 74px;
      }
    }

    &__btn-play {
      margin-right: 40px;
      @media screen and (min-width: 768px) {
        margin-right: 16px;
      }

      &:hover {
        img {
          box-shadow: 0 0 12px #fff;
          transform: scale(0.9);
          @include transition02;
        }
      }

      img {
        border-radius: 50%;
        @include transition02;
        margin-right: 6px;
        width: 29px;
        height: 29px;
        @media screen and (min-width: 768px) {
          width: 71px;
          height: 71px;
          margin-right: 0;
          margin-bottom: 8px;
        }
      }

      p {
        color: #fff;
        @include Nunito;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        @media screen and (min-width: 768px) {
          font-size: 18px;
          line-height: 25px;
        }
      }
    }

    &__social {
      &_item {
        img {
          width: 18px;
          height: 19px;
          @media screen and (min-width: 768px) {
            width: 23px;
            height: 24px;
          }
        }

        @include transition02;

        &:hover {
          opacity: 0.6;
          @include transition02;
        }

        margin-right: 8px;
        @media screen and (min-width: 768px) {
          margin-bottom: 12px;
          margin-right: 0;
        }
        @media screen and (min-width: 992px) {
        }
      }
    }
  }
}

.about-hotel {
  padding-top: 32px;
  padding-bottom: 45px;
  @media screen and (min-width: 768px) {
    padding-top: 26px;
    padding-bottom: 60px;
  }
  @media screen and (min-width: 992px) {
    padding-top: 18px;
    padding-bottom: 45px;
  }
  background-image: url(../img/landing/bg-about-honel.png);

  &__tabs {
    @media screen and (min-width: 768px) {
      margin-bottom: 51px;
    }

    &_item {
      margin-bottom: 18px;
      position: relative;
      @include Nunito;
      padding-bottom: 16px;
      font-weight: 400;
      font-size: 16px;
      line-height: 22px;
      @include transition02;
      border-bottom: 2px solid #fff;

      @media screen and (max-width: 767px) {
        border-bottom: 2px solid #fff279;
        &::after {
          position: absolute;
          content: "";
          display: block;
          width: 18px;
          height: 12px;
          top: 6px;
          right: 0;
          background-image: url(../img/icons/arrow-down-white.svg);
          background-size: 18px 12px;
        }
      }
      @media screen and (min-width: 768px) {
        margin-bottom: 0;
        text-align: center;
        width: 173px;
      }

      &.active {
        border-bottom: 2px solid #fff279;
      }

      &:hover {
        cursor: pointer;
        @include transition02;
        border-bottom: 2px solid #fff279;
        color: #fff279;
      }
    }
  }

  &__basic {
    margin-bottom: 32px;

    &_item {
      @include OpenSans;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 16px;
      @media screen and (min-width: 768px) {
        width: 240px;
        margin-bottom: 0;
      }

      img {
        width: 26px;
        margin-right: 32px;
        @media screen and (min-width: 768px) {
          margin-right: 16px;
        }
      }
    }
  }

  &__comfort {
    &_col {
      @media screen and (min-width: 768px) {
        margin-right: 24px;
        margin-bottom: 40px;
      }

      &:last-child {
        @media screen and (max-width: 767px) {
          margin-bottom: 48px;
        }
      }
    }

    &_item {
      @include OpenSans;
      font-weight: 400;
      font-size: 12px;
      line-height: 15px;
      margin-bottom: 16px;
      @media screen and (min-width: 768px) {
        margin-bottom: 22px;
      }

      img {
        width: 26px;
        margin-right: 16px;
      }
    }
  }

  &__title {
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      margin-bottom: 24px;
    }
    @include OpenSans;
    font-weight: 800;
    font-size: 18px;
    line-height: 25px;
  }

  &__desc {
    &_text {
      @include OpenSans;
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 40px;
      @media screen and (min-width: 768px) {
        margin-bottom: 0;
        margin-right: 20px;
      }
      @media screen and (min-width: 1200px) {
        margin-right: 0;
      }
    }
  }

  .rating-hotel {
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 19px;
    background-color: #fff;
    padding: 28px 16px 32px;
    margin-bottom: 40px;
    @media screen and (min-width: 768px) {
      margin-bottom: 0;
      min-width: 347px;
      padding: 28px 24px 36px;
    }
    @media screen and (min-width: 1200px) {
      position: absolute;
      right: 0;
      top: 84px;
    }
  }
}

.rating-hotel {
  @include Nunito;
  color: #000 !important;

  &__title {
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    margin-bottom: 20px;
  }

  &__feedback {
    margin-bottom: 18px;
  }

  &__number {
    padding: 12px 8px;
    border: 1px solid rgba(0, 0, 0, 0.7);
    border-radius: 8px;
    margin-right: 8px;

    &_current {
      font-weight: 700;
      font-size: 20px;
      line-height: 1;
      color: #4db53c;
    }

    &_max {
      font-weight: 400;
      font-size: 13px;
      line-height: 1;
      color: rgba(0, 0, 0, 0.7);
    }
  }

  &__estimate {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 2px;
    color: rgba(0, 0, 0, 0.7);
  }

  &__review {
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    color: rgba(0, 0, 0, 0.6);
    opacity: 0.7;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    @include transition02;

    &:hover {
      @include transition02;
      opacity: 1;
      color: #000;
    }
  }

  &__info {
    &_item {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 40px;
      }
    }

    &_text {
      width: 45%;
      white-space: nowrap;
      margin-right: 8px;
      margin-bottom: 0;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      color: rgba(0, 0, 0, 0.7);
      @media screen and (max-width: 767px) {
        font-size: 14px;
        line-height: 18px;
      }
    }

    &_range {
      width: 55%;

      &-fill {
        width: 116px;
        height: 7px;
        border-radius: 50rem;
        background-color: #aeaeae;
        margin-right: 8px;

        span {
          border-radius: 50rem;
        }
      }

      &-number {
        width: 24px;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: rgba(0, 0, 0, 0.6);
        opacity: 0.7;
      }
    }
  }

  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    padding: 10px 25px;
    background-color: #fff279;
    border-radius: 16px;
    border: 1px solid #fff279;
    @include OpenSans;

    &:hover {
      background-color: #fff;
    }
  }
}

.hostel-rules {
  margin-top: 100px;
  margin-bottom: 120px;

  .main-landing__heading {
    margin-bottom: 45px;
  }

  &__list {
    max-height: 250px;
    @media screen and (min-width: 992px) {
      max-width: 66.66666%;
    }

    &_item {
      position: relative;
      padding-left: 30px;
      max-width: 370px;
      margin-bottom: 20px;
      font-size: 18px;
      line-height: 25px;
      @include OpenSans;
      font-weight: 400;

      &:last-child {
        font-weight: 600;
      }

      &::after {
        position: absolute;
        content: url(../img/icons/check-blue.svg);
        top: 0;
        left: 0;
      }
    }
  }
}

.contacts-landing {
  margin-top: 32px;
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    margin-top: 64px;
    margin-bottom: 64px;
  }
  @media screen and (min-width: 992px) {
    margin-top: 144px;
    margin-bottom: 110px;
  }

  .main-landing__heading {
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      margin-bottom: 70px;
    }
  }

  &__info {
    @media screen and (min-width: 992px) {
      margin-bottom: 54px;
    }
  }

  &__tel {
    white-space: nowrap;
    width: 100%;
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      margin-bottom: 54px;
      width: 30%;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 0;
      width: auto;
      margin-right: 70px;
    }

    img {
      padding-top: 4px;
      width: 20px;
      margin-right: 16px;
    }

    &_item {
      color: #000000;
      @include OpenSans;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;

      &:hover {
        color: #fec87e;
        @include transition02;
      }
    }
  }

  &__mail {
    white-space: nowrap;
    width: 100%;
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      margin-bottom: 54px;
      width: 70%;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 0;
      width: auto;
      margin-right: 70px;
    }

    img {
      padding-top: 4px;
      width: 20px;
      margin-right: 16px;
    }

    &_item {
      @include OpenSans;
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
      color: #000000;

      &:hover {
        color: #fec87e;
        @include transition02;
      }
    }
  }

  &__desc {
    color: #000000;
    font-weight: 700;
    font-size: 17px;
    line-height: 25px;
    @include OpenSans;
    width: 100%;
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      font-size: 18px;
      margin-bottom: 54px;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 0;
      width: auto;
      max-width: 500px;
    }
  }
}

.contacts-map {
  &__info {
    &_wrapper {
      padding: 30px;
      @media screen and (min-width: 768px) {
        padding: 64px;
      }
    }

    max-width: 230px;
    background-color: #fff;
    box-shadow: 0px 2px 30px rgba(47, 76, 88, 0.06);
    border-radius: 8px;
    padding: 40px 24px;
    @include Mulish;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #040b11;
    @media screen and (max-width: 767px) {
      margin-left: auto;
      margin-right: auto;
    }

    &_item {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }

      img {
        margin-right: 8px;
      }
    }
  }

  &__iframe {
    object-fit: cover;
    height: 500px;
    @media screen and (min-width: 768px) {
      height: 376px;
    }
  }
}

.reviews {
  &__heading {
    .main-landing__heading {
      margin-bottom: 28px;
      @media screen and (min-width: 768px) {
        margin-bottom: 68px;
      }
      @media screen and (min-width: 992px) {
        margin-bottom: 34px;
      }
    }

    .rating-hotel {
      background: #ffffff;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 19px;
      padding: 28px 16px;
      margin-bottom: 24px;
      @media screen and (min-width: 768px) {
        margin-bottom: 32px;
        padding: 12px 24px 24px;
      }
      @media screen and (min-width: 992px) {
        padding: 16px 32px 30px;
        margin-bottom: 40px;
      }

      &__info {
        @media screen and (min-width: 768px) {
          margin-left: auto;
        }
        @media screen and (min-width: 992px) {
          max-height: 100px;
          margin-left: 0;
        }

        &_item {
          @media screen and (min-width: 992px) {
            margin-right: 80px;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      &__feedback {
        @media screen and (min-width: 768px) {
          margin-right: 80px;
        }
      }
    }
  }

  .rating-hotel__info_range {
    width: 130px;
  }
}

.reviews-user {
  position: relative;
  background: #ffffff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  padding: 25px 14px;
  @include Nunito;
  font-weight: 400;
  margin-bottom: 24px;
  @media screen and (min-width: 768px) {
    padding: 28px 56px 38px 120px;
    margin-bottom: 32px;
  }
  @media screen and (min-width: 992px) {
    padding: 26px 44px 40px 100px;
  }
  @media screen and (min-width: 1200px) {
    padding: 26px 84px 40px 166px;
  }

  &:last-child {
    margin-bottom: 32px;
    @media screen and (min-width: 768px) {
      margin-bottom: 45px;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 60px;
    }
  }

  &__heading {
    @media screen and (min-width: 992px) {
      margin-bottom: 48px;
    }
  }

  &__info {
    margin-bottom: 20px;
    width: 100%;
    @media screen and (min-width: 768px) {
      margin-bottom: 36px;
    }
    @media screen and (min-width: 992px) {
      margin-bottom: 0;
      max-width: 266px;
      margin-right: 50px;
    }
    @media screen and (min-width: 1200px) {
      margin-right: 70px;
    }
  }

  &__ava {
    margin-right: 14px;
    @media screen and (min-width: 768px) {
      position: absolute;
      top: 28px;
      left: 32px;
    }
    @media screen and (min-width: 992px) {
      margin-right: 0;
      top: 24px;
      left: 24px;
    }

    img {
      width: 50px;
      @media screen and (min-width: 1200px) {
        width: 80px;
      }
    }
  }

  &__name {
    font-weight: 700;
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 2px;
    @media screen and (min-width: 768px) {
      margin-bottom: 0;
      margin-right: 12px;
    }
  }

  &__people {
    img {
      margin-right: 2px;
    }

    @media screen and (max-width: 767px) {
      margin-bottom: 8px;
    }
  }

  &__apartment {
    margin-bottom: 0;
    font-size: 16px;
    line-height: 22px;
    color: #484848;
    opacity: 0.7;
    margin-top: 20px;
    width: 100%;

    p {
      max-width: 266px;
    }

    @media screen and (min-width: 768px) {
      margin-top: 12px;
    }
    @media screen and (min-width: 992px) {
      margin-top: 8px;
    }
  }

  &__good {
    @media screen and (max-width: 767px) {
      margin-bottom: 26px;
      width: 100%;
    }
    @media screen and (min-width: 768px) {
      max-width: 258px;
      margin-right: 50px;
      margin-bottom: 32px;
    }
    @media screen and (min-width: 992px) {
      max-width: 258px;
      margin-right: 30px;
      margin-bottom: 0;
    }
    @media screen and (min-width: 1200px) {
      margin-right: 50px;
    }

    &_title {
      font-weight: 700;
      font-size: 18px;
      line-height: 25px;
      color: rgba(0, 0, 0, 0.7);
      margin-bottom: 8px;
    }

    &_desc {
      color: rgba(0, 0, 0, 0.7);
      font-size: 16px;
      line-height: 22px;
      @media screen and (min-width: 768px) {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }

  &__bad {
    @media screen and (max-width: 767px) {
      margin-bottom: 30px;
      width: 100%;
    }
    @media screen and (min-width: 768px) {
      max-width: 228px;
      margin-bottom: 32px;
    }
    @media screen and (min-width: 992px) {
      max-width: 228px;
      margin-bottom: 0;
    }

    &_title {
      font-weight: 700;
      font-size: 18px;
      line-height: 25px;
      color: rgba(0, 0, 0, 0.7);
      margin-bottom: 8px;
    }

    &_desc {
      color: rgba(0, 0, 0, 0.7);
      font-size: 16px;
      line-height: 22px;
      @media screen and (min-width: 768px) {
        font-size: 18px;
        line-height: 25px;
      }
    }
  }

  &__desc {
    color: rgba(0, 0, 0, 0.7);
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 12px;
    @media screen and (min-width: 768px) {
      font-size: 18px;
      line-height: 25px;
    }

    &_show {
      position: relative;
      color: rgba(0, 0, 0, 0.7);
      font-weight: 700;
      font-size: 18px;
      line-height: 25px;
      @include transition02;

      &:hover {
        @include transition02;
        color: #235784;
      }

      &::after {
        position: absolute;
        content: "";
        top: -300%;
        left: 0;
        width: 100%;
        height: 300%;
        background: #fff;
        background: linear-gradient(
                        -180deg,
                        rgba(255, 255, 255, 0) 0%,
                        #ffffff 100%
        );
      }
    }
  }
}

.bottom-navigation {
  margin-bottom: 40px;
  @media screen and (min-width: 768px) {
    margin-bottom: 100px;
  }

  &__item {
    @include OpenSans;
    font-weight: 600;
    font-size: 16px;
    line-height: 30px;
    text-align: center;
    width: 30px;
    height: 30px;
    margin-right: 4px;
    color: #2c3c4a;
    border-radius: 4px;
    @include transition02;

    &.active,
    &:hover {
      @include transition02;
      color: #fff;
      background: #235784;
    }
  }
}
